import React from 'react';
import {connect} from 'react-redux';
import {Button} from "reactstrap";

const SelectiveColour = ({id, startParsing}) => {
    return <Button onClick={startParsing}> HUUUNDIII</Button>

};


const mapStateToProps = state => {
    return {
        id: 1
    };
};

const mapDispatchToProps = {
    startParsing: () => {}
};

export default connect(mapStateToProps, mapDispatchToProps)(SelectiveColour);




